.invit-table .ant-card-body {
  padding: 0;
}
.invit-table .dx-money {
  text-align: right !important;
}
.invit-table .invit-con {
  border: 1px solid rgba(0, 0, 0, 0.06);
  box-shadow: 0px 0px 20px 0px rgba(246, 246, 246, 0.78);
  border-radius: 8px;
}
.invit-table .invit-con .invit-tit {
  padding: 22px 34px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.06);
}
.invit-table .invit-con .invit-tit .font-h2 {
  font-size: 20px;
}
@media only screen and (max-width: 768px) {
  .invit-table .invit-con .invit-tit .font-h2 {
    font-size: 16px;
  }
}
@media only screen and (max-width: 768px) {
  .invit-table .invit-con .invit-tit {
    padding: 6px 15px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.06);
  }
}
.invit-table .invit-con .table-col {
  padding: 24px 30px;
}
@media only screen and (max-width: 768px) {
  .invit-table .invit-con .table-col {
    padding: 0 0 24px 0px;
  }
}
.invit-table .ant-table-thead > tr > th {
  white-space: nowrap;
}
.invit-table .ant-table-row td {
  white-space: nowrap;
}
