.div1 {
  color: #000;
  background-color: #fff;
}
.div1 .share-titles {
  margin-bottom: 32px;
}
.div1 .share-title {
  font-size: 20px;
  font-weight: bold;
  display: inline-block;
}
.div1 .explain {
  float: right;
  line-height: 30px;
  cursor: pointer;
}
.div1 .title-0 {
  margin-bottom: 8px;
}
.div1 .title-1 {
  padding-right: 24px;
  margin-bottom: 32px;
}
.div1 .title-2 {
  width: 90%;
}
.div1 .copy-max {
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.06);
  overflow: hidden;
  height: 48px;
  border-radius: 4px;
  line-height: 48px;
  font-size: 14px;
}
.div1 .copy-max .address {
  display: inline-block;
  margin-left: 17px;
  width: 80%;
  overflow: hidden;
  /*超出部分隐藏*/
  white-space: nowrap;
  /*让文本强制在一行不换行*/
  text-overflow: ellipsis;
  /*显示省略号来代表被修剪的文本*/
}
.div1 .copy-max .copy {
  color: #0091FF;
  float: right;
  padding: 0 28px;
  cursor: pointer;
}
.div1 .invitations-num {
  border: 1px solid rgba(0, 0, 0, 0.06);
  height: 70px;
  background: #ffffff;
  box-shadow: 0px 0px 20px 0px rgba(246, 246, 246, 0.78);
  border-radius: 4px;
  margin-top: 19px;
}
.div1 .invitations-num .img-1 {
  width: 32px;
  height: 32px;
  box-shadow: 0px 0px 4px 0px #d9ceff;
  border-radius: 50%;
  display: inline-block;
  vertical-align: super;
  margin: 0 11px 0 24px;
}
.div1 .invitations-num .num-center {
  display: inline-block;
  line-height: 20px;
  margin-top: 18px;
  height: 100%;
}
.div1 .invitations-num .num-center .center-1 {
  display: block;
}
.div1 .invitations-num .num-center .center-2 {
  display: inline-block;
  font-weight: bold;
}
.div1 .invitations-num .bonus {
  line-height: 70px;
  vertical-align: top;
  text-align: right;
  height: 100%;
  float: right;
  color: #0091FF;
  margin-right: 17px;
}
.div1 .copy-max2 {
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.06);
  overflow: hidden;
  height: 48px;
  border-radius: 4px;
  line-height: 48px;
  font-size: 14px;
}
.div1 .copy-max2 .address2 {
  display: inline-block;
  margin-left: 17px;
  overflow: hidden;
  /*超出部分隐藏*/
  white-space: nowrap;
  /*让文本强制在一行不换行*/
  text-overflow: ellipsis;
  /*显示省略号来代表被修剪的文本*/
}
.div1 .copy-max2 .copy2 {
  color: #0091FF;
  float: right;
  text-align: center;
  width: 42%;
  cursor: pointer;
}
.div1 .title-3 .share-input {
  display: inline-block;
  height: 48px;
  width: 100%;
}
.div1 .title-3 .share-input .input-1 {
  display: inline-block;
  width: 80%;
  height: 48px;
}
.div1 .title-3 .share-input .share-button {
  display: inline-block;
  height: 48px;
  width: 20%;
}
.div1 .title-3 .share-input .ant-input {
  border-radius: 4px 0 0 4px;
}
.div1 .title-3 .share-input .ant-btn {
  border-radius: 0 4px 4px 0;
}
.div1 .yesterday {
  margin-top: 28px;
}
.div1 .yesterday .invitations-num1 {
  border: 1px solid rgba(0, 0, 0, 0.06);
  height: 70px;
  background: #ffffff;
  box-shadow: 0px 0px 20px 0px rgba(246, 246, 246, 0.78);
  border-radius: 4px;
}
.div1 .yesterday .invitations-num1 .img-1 {
  width: 32px;
  height: 32px;
  box-shadow: 0px 0px 4px 0px #d9ceff;
  border-radius: 50%;
  display: inline-block;
  vertical-align: super;
  margin: 0 11px 0 24px;
}
.div1 .yesterday .invitations-num1 .num-center {
  display: inline-block;
  line-height: 20px;
  margin-top: 18px;
  height: 100%;
}
.div1 .yesterday .invitations-num1 .num-center .center-1 {
  display: block;
}
.div1 .yesterday .invitations-num1 .num-center .center-2 {
  display: inline-block;
  font-weight: bold;
}
.div1 .yesterday .invitations-num1 .bonus {
  line-height: 70px;
  vertical-align: top;
  text-align: right;
  height: 100%;
  float: right;
  color: #0091FF;
  margin-right: 17px;
}
.div1 .share-details {
  font-size: 20px;
  font-weight: bold;
  margin: 30px 0 15px 0;
}
@media only screen and (max-width: 768px) {
  .div1 .title-1 {
    padding-right: 0px;
    margin-bottom: 0px;
  }
  .div1 .title-2 {
    width: 100%;
  }
  .copy2 {
    width: 19% !important;
  }
  .div1 .share-title {
    margin-bottom: 0px;
  }
  .div1 .copy-max .copy {
    color: #0091FF;
    float: right;
    padding: 0 ;
    width: 19%;
    text-align: center;
  }
  .div1 .copy-max .address {
    display: inline-block;
    margin-left: 17px;
    width: 75%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}
